*,
*::before,
*::after {
  box-sizing: border-box;
}

html {
  font-size: 16px;
}

html,
body {
  min-height: 100%;
  position: relative;
}

body {
  color: #000;
  font-family: 'Montserrat', sans-serif;
  font-size: 1rem;
  font-weight: 400;
  line-height: 1.4;
}

ul,
ol,
li,
p,
h1,
h2,
h3,
h4,
h5,
h6 {
  margin: 0;
}

img {
  display: block;
  max-width: 100%;
}

input {
  background-color: transparent;
  border-radius: 20px;
  border: 1px solid #000;
  height: 60px;
  font-size: 18px;
  padding: 30px 20px;
  max-width: 210px;
  width: 100%;
}

.title {
  font-size: 64px;
  font-weight: 700;
  line-height: 1.1;

  span {
    background-image: linear-gradient(90.61deg, #DEB15A 3.38%, #EBCD80 52.67%);
    background-clip: text;
    -webkit-text-fill-color: transparent;
  }
}

.btn {
  appearance: none;
  border: none;
  border-radius: 20px;
  background-color: transparent;
  cursor: pointer;
  display: inline-block;
  padding: 22px 34px;
  margin: 0;
  height: 70px;
  text-align: center;
  text-decoration: none;

  & + & {
    margin-left: 30px;
  }

  &--xs {
    height: 60px;
    padding: 17px 35px;
  }

  &--primary {
    color: #000;
    background-image: linear-gradient(91.75deg, #FBC502 17.87%, #FFD644 90.21%);
    font-size: 20px;
    font-weight: 600;
  }

  &--secondary {
    background-color: #fff;
    border: 1px solid #D8D8D8;
  }

  &--more {
    background-image: linear-gradient(90deg, #4D5571 6.78%, #454957 96.11%);
    color: #fff;
    font-size: 20px;
    font-weight: 600;
  }

  &--more-xs {
    color: #878787;
    background-color: #fff;
    border: 1px solid;
    height: 53px;
    padding: 16px 30px;
    font-size: 16px;
  }
}

.has-arrow {
  position: relative;
  width: fit-content;

  &::after {
    content: '';
    position: absolute;
    left: calc(100% + 13px);
    bottom: 50%;
    display: block;
    width: 32px;
    height: 34px;
    background-image: url("data:image/svg+xml;base64,");
  }
}

.section-head {
  margin-bottom: 55px;

  &--num {
    display: flex;
  }

  &__num {
    color: #D8E5ED;
    margin-right: 45px;
    font-size: 148px;
    font-weight: 300;
    line-height: 120px;
  }

  &__title {
    font-size: 48px;
    font-weight: 700;
    line-height: 1.1;
    margin-bottom: 15px;
  }

  &__sub {
    font-size: 24px;
    line-height: 1.3;
  }
}

.container {
  position: relative;
  margin: 0 auto;
  max-width: 1440px;
  width: 100%;
}

.row {
  margin: 0 -15px;
  display: flex;
}

[class^="col"] {
  padding: 0 15px;
}

.col {
  width: 100%;
}